.bmpay {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
}

/* ì„¹ì…˜ ì™¼ìª½ */
.pay-left {
    width: 50%;
    height: 100vh;
    background-color: rgba(42, 193, 188, 0.45);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.pay-img {
    width: 40vw;
    max-width: 500px;
}
.pay-left>p {font-size:clamp(1.3rem, 6vw, 2.5rem);}

/* ì„¹ì…˜ ì˜¤ë¥¸ìª½*/
.pay-right {
    width: 50%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0px;
}

/* íŽ˜ì´ë¨¸ë‹ˆ í…ìŠ¤íŠ¸ */
.pay-title {
    font-size: clamp(1.5rem, 4vw, 4rem);
    text-align: center;
    line-height: 170%;
    /* line-height: 90px; */
}
.pay-font-big {
    font-size: 1.2em; 
}
.pay-subtitle {
    text-align: center;
    font-size: clamp(1rem, 2vw, 2.5rem);
    color: #2ac1bc; 
}
.qr-text {
    font-size: clamp(1rem, 2vw, 2rem);
}

/* ë©”ì´ë¨¸ë‹ˆ ì´ë¯¸ì§€ */
.pay-right >img {
    width: 30vw;
    max-width: 400px;
}





/* ë°˜ì‘í˜• ë¯¸ë””ì–´ì¿¼ë¦¬ */
/* 768px ì´í•˜: ì„¹ì…˜ ìœ„ì•„ëž˜ ì •ë ¬ */
@media (max-width: 768px) {
  .bmpay {
    flex-direction: column;
    height: 100vh;
  }
  .pay-left,
  .pay-right {
    width: 100%;
    height: auto;
    padding: 40px 0;
  }
  .pay-img {
    max-width: 200px;
  }
  .pay-right {
    padding-top: 15%;
  }
  .pay-right > img {
    width: 50vw;
    max-width: 350px;
  }
}

/* 500px ì´í•˜: í¬ê¸° ì¡°ì • */
@media (max-width: 500px) {
  .pay-right > img {width: 60vw;}

  .pay-title {font-size: 1.5rem;}

  .pay-subtitle,
  .qr-text {
    font-size: 16px;
  }
}

