.intro {
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to top, 
 rgba(42, 193, 188, 0.45) 0%,
 rgba(42, 193, 188, 0.45) 25%,
 white 25%,
 white 100%);
  overflow: hidden;
}

